    <Typography paragraph>第一，中国民谣的风格并不像我们熟知的美式民谣(Country)那样统一。这很大程度上得益于我国悠久的历史和复杂的地理环境，从传统民谣这个角度来看，地域差异和民族差异导致了同是民谣二字你听到的可能是《信天游》也可能是《送别》。第二，中国目前并没有太多在继承以及继续创作传统民谣的主流乐队（有经济公司/音乐厂牌，有已发行唱片）。第三，也是奇怪的主要原因，目前主流且从2010年至今十几年来流行的民谣音乐们，更像是摇滚乐中颓废情绪和地摊文学的杂交产品。我们必须尊重的事实是，现在成为中国流行乐分支的“民谣音乐”是在相当长的一段时间得到了广泛传唱和流行的。但它既不服从台湾民歌运动带动的大陆校园民谣的统治，也与各地方民俗歌谣毫无关系，看起来更像是一个低成本的摇滚乐输出手段。因为到头来你只需要一把吉他，甚至都不需要一把吉他。</Typography>
     
