    <Typography paragraph>或许是良心发现，17年嘻哈音乐在祖国大地像一颗惊雷炸响，通过坐上网络综艺快车蚕食并吞并了泔水民谣的领地。诚然，祖国的嘻哈音乐无论从质量还是技术上都还是显得稚嫩青涩，中文和匪帮文化/说唱节奏都并不兼容，说唱本地化并不成功。但是好在无论如何说唱音乐有非常强的节奏支持，能适应大量无需内容的音乐场景，也非常轻松的避免了成为泔水。在2017-2019年，我甚至快忘了这世界上还有一批顶级泔水制造者在潜伏，更不会明白他们中间正经历着王座更迭，一个暴君即将登基。</Typography>
     
