    <Typography paragraph>我们常说这是个功利的时代，皇帝做事利在千秋，所以我们从功利的角度讲要无条件支持。樊帝作为一个歌手通过泔水民谣在这个时代的成功，是每一个年轻人都渴望而不可及的。从另一个角度来说，我甚至觉得樊帝并不是真的喜欢泔水民谣，就像俞敏洪不是真的喜欢英语，马化腾也未必爱玩农药一样。商人到头来只论收益，樊帝刚好就做到了这点，我实在不能去责备这样一个年少有为的杰出青年。那么回头来看，是谁让劣币驱逐了良币呢。是正在阅读这个回答的你们，也是我们。是整个华语流行音乐的听众群体低劣的听觉审美，让樊帝走到了民谣金字塔尖，和流行音乐部门的众偶像们一起为音乐市场制造垃圾，填补着人们内心空白。</Typography>
     
