import React,{useState} from 'react';
import Header from './Header'
import Critics from '../articles/critics/Critics'
import './home.css'


export default function Home(props) {
    const[menuItems,setMenus] = useState(['地崩山摧壮士死', '然后天梯石栈相勾连', '锦城虽云乐', '不如早还家']);
    const[currentMenu,setCurrentMenu] = useState();
    

    
    function menuItemClick(index){
          setCurrentMenu(index)
    }

    return <div class="container">
        <Header menuItems={menuItems} onClick={menuItemClick}></Header>
      
        {currentMenu === 0 && <Critics></Critics>}<Critics></Critics>
        
    </div>
}